import sortBy from 'lodash/sortBy';
import { COMPUTED_METRICS_LABELS, COMPUTED_METRICS_PRIORITY } from '../../../specs';
export function collectOptionByType(list, type) {
    var options = (list || [])
        .filter(function (attr) {
        if (Array.isArray(type))
            return type.includes(attr.type);
        return attr.type === type;
    })
        .map(function (attr) {
        return {
            value: attr.name,
            label: COMPUTED_METRICS_LABELS[attr.name] || attr.name
        };
    });
    options = sortBy(options, function (option) { return COMPUTED_METRICS_PRIORITY[option.value] || option.value; });
    return options;
}
export function countNestedOptions(options) {
    return options.reduce(function (count, group) {
        return count + group.options.length;
    }, 0);
}
